.dialogsArea{
    height: 400px;
    margin: 10px 10px 10px 10px;
    background-color: #ffffff;
    padding: 3px;
}

.dialogs {
    display: flex;
    flex-flow: row;
    margin-left: 20px;
}

.names{
    padding: 5px;
    margin-right: 10px;
}
.name{
    color: #2a588b!important;
    text-decoration: none;
    font-weight: bold;
}
.name:hover{
    color: #2a588b!important;
    text-decoration: underline  ;
    font-weight: bold;
}

.messages {
    padding: 5px;
    color: #2a588b;
    width: 300px!important;
}
.textArea, .buttonAddPost{
    height: 20px;
    margin: 10px 0;
    margin-right: 5px;
    display: inline-block;
    vertical-align: top;
}

.dialogsArea h2{
    color: #2a588b;
    background-color: #edeef0;
    padding:5px 10px 5px 20px ;
    margin-bottom: 10px;
}
.textArea{
    margin-left: 20px;
}

.buttonArea{
    margin-top: 10px;
}